/* Mobile responsiveness */
@media only screen and (max-width: 768px) {
  .container {
    padding: 10px;
  }

}


body {
  background-color: #d0e1f3;
  /* width:768px; */
  height: 100vh;
  /*height:1024px*/
}

button.dayEvent {
  /* border: 0; */
  display: flex;
  flex-direction: column;
  padding: 2px;
  min-height: 95%;
  width: 100%;
}

h2 {
  background-color: aliceblue;
  padding: 2%;
  text-align: center;
  width: 100%;
}

h5 {
  background-color: azure;
  font-size: large;
  margin: 0;
  text-align: center;
}

h3 {
  margin: 0;
}

input {
  font-size: 24px;
  margin: 0px 10px;
}

label {
  display: flex;
  font-size: 24px;
  margin: 10px;
}

.workoutList {
  margin-top: 4px;
  background-color: #10739e;
  border-radius: 5px;
  padding: 2px;
  margin-bottom: 5px;
  color: aliceblue;
}

Link,
a {
  text-decoration: none;
  color: black;
}

 .banner{

margin-bottom: -30px;
  text-align: center;
} 

/* For Calendar.js */
.calendar {
  margin-right: 10px;
  scale: 94%;
  align-items: center;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  
  width: 500px;
 
}

.day {
  
  /* border: navy 3px solid; */
  margin: 1%;
  /* padding: 1%; */
  height: 400px;
  width: 100px;
}

.event {
  background-color: #10739e;
  border: 2px solid rgb(22, 22, 136);
  color: rgb(255, 255, 255);
  font-size: 18px;
  /* font-weight: bold; */
  margin: 10px 0;
  border-radius: 3px;
}

.eventsList {
  align-items: center;
  border: 2px solid #10739e;
  border-radius: 5px;
  background-color: white;
  border: 3px solid darkblue;
  display: flex;
  flex-direction: column;
  margin: 15px;
  /* width: 95%; */
}

.eventListItem {
  align-items: center;
  background-color: lightblue;
  border: 2px solid rgb(22, 22, 136);
  display: flex;
  font-size: 18px;
  justify-content: center;
  /* margin: 10px 200px; */
  padding: 10px;
  /* width:300px; */
}

#eventOptions {
  padding-left: 100px;
}

.gridcolumn {
  padding: 0px;
}

.column {
  border-bottom: 2px solid gray;
  display: grid;
  margin: 0px;
  padding: 20px 0px;
}

.schedule {
  background-color: red;
  position: relative;
  top: -30%;
}

/* Activities */
.activities {
  margin-right: 5%;
}

.info {
  background-color: lightblue;
  border-radius: 7px;
  margin-top: -80px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5%;
  border: #10739e 2px solid;
  height: 140px;
  width: 200px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .3);
}

.info:hover {
  height: 145px;
  width: 205px;
  transition: all 0.1s ease-in;
  font-size: 1.1em;
}

.info2 {

  background-color: lightblue;
  border-radius: 7px;
  margin-top: -5px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5%;
  border: #10739e 2px solid;
  height: 75px;
  width: 200px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .3);

}

.info2:hover {
  height: 80px;
  width: 205px;
  transition: all 0.1s ease-in;
  font-size: 1.1em;
}


.info-panels {
  display: flex;
  justify-content: center;
  padding: 2%;
}

/* Navbar */
.navbar {
  background-color: dodgerblue;
  border: 3px solid dodgerblue;
  color: white;
  font-size: larger;
  font-weight: bold;
  height: 80px;
  margin: 0;
  justify-content: center;
  width: 100%;
}

/* Schedule Evnet Form */
.formButton {
  background-color: #10739e;
  border-radius: 10px;
  color: rgb(255, 255, 255);
  font-size: 1em;
  /* margin-right: 100px; */
  padding: 5px;
  width: 100px;
}

.formButtons {
  display: flex;
  justify-content: space-evenly;
}

.scheduleEvent {
  align-items: center;
  border: 2px solid #10739e;
  border-radius: 5px;
  background-color: white;
  justify-content: center;
  margin: 15px;
  /* margin-top: -40px; */

}

.label {
  margin: 5px;
  font-size: .2em;
}

.formInput {
  font-size: 1em;
  height: 17px;
  width: 150px;
}

p {
  margin-top: -1px
}

.scheduleExercise {
  background-color: lightblue;
  display: grid;
  padding: 10px;
  margin: 10px;
}

#submit {
  background-color: #4caf50;
}

#submit:hover {
  background-color: #45a049;
}

#cancel {
  background-color: #fc5145;
}

#cancel:hover {
  background-color: #941414;
}

#scheduleTitle {
  text-align: center;
  width: 95%;
}

.button {
  background-color: lightblue;
  border: 0px;
  margin-top: -10px;

}

.time {
  display: flex;
  margin: 0;
  padding: 0;
}

.tik {
  display: flex;
  align-items: center;
  background-color: black;
  color: white;
  height: 30px;
  font-size: 1.5em;
  border-radius: 8px;
}

.t {
  display: flex;
  align-items: center;
  margin-top: 15px;
  padding: 25%;
}